@import "../../../style/variables";

.block.contacts-form {

  .error-message {
    display: inline-block;
    width: 100%;
    text-align: center;
    color: $primary;
  }

  .input-group,
  .phone-container {
    width: 75%;
    margin: auto auto 20px;

    @media (max-width: $media-small) {
      width: 100%;
    }

    .react-phone-number-input__icon {
      border: 0;
      height: auto;
    }

    input {
      height: calc(1.5em + .75rem + 2px);
      padding: .375rem .75rem;
    }

    input, textarea {
      border-radius: 0;
      background: #666;
      border: 0;
      color: white;
      &::placeholder {
        color: #AAA;
      }
    }
  }

  .submit {
    cursor: pointer;
    display: inline-block;
    color: $primary;
    width: 100%;
    text-align: right;
    padding-right: 12.5%;
    font-size: 24pt;

    @media (max-width: $media-medium) {
      padding: 0;
    }
  }
}

