@import "../../style/variables.scss";

$link-color: #666;
$stroke-width: 1px;
$caret-stroke-width: 3px;

.aside-container {
    position: fixed;
    width: 80px;
    height: 80%;
    left: 0;
    top: 10%;

    @media (max-width: $media-medium){
        display: none;
    }
}

.aside {
    position: relative;
    width: $stroke-width;
    left:40px;
    height: 80%;
    top: 10%;

    @media (max-width: $media-medium){
        left: 20px;
    }
}

.aside:after {
    content: '';
    display: block;
    background: $link-color;
    position: absolute;
    left: -10px;
    width: 20px;
    height: $stroke-width;
    bottom: 0;
}

.link {
    cursor: pointer;
    user-select: none;
    position: relative;
    margin: 0;
    border-left:$stroke-width solid $link-color;
    height: 25%;
    width: 100px;
    color: $link-color;
    font-weight: bolder;
    transition: background-color .5s;
}

.link span {
    position: absolute;
    left: 10px;
    top: calc(50% - 8px);
    font-size: 16px;
    vertical-align: middle;
    line-height: 100%;
    transition: color .5s;
}

.link span:before{
    content: attr(data-index);
    left: -35px;
    position: absolute;
    font-family: arial, serif;
    font-weight: 100;

    @media (max-width: $media-medium){
        display: none;
    }
}

.link.active span{
    color: $primary;
}

.link:before,
.link:after{
    top: 0;
    content: '';
    opacity:1;
    position: absolute;
    left: -3px;
    display: block;
}

.link:before {
    width: 20px;
    height: $stroke-width;
    left: -10px;
    background: $link-color;
}

.link:after{
    content: '';
    left: -5px;
    height: calc(100% + 6px);
    width: 10px;
    background: repeating-linear-gradient(0deg,transparent,transparent 25%,$link-color 25%, $link-color calc(25% + 1px));
}

.caret {
    cursor: pointer;
    user-select: none;
    margin: 0;
    border-left:$caret-stroke-width solid $primary;
    height: 25%;
    width: 100px;
    color: $link-color;
    font-weight: bolder;
    position: absolute;
    top: 1px;
    left: -1px;
    z-index: 2;
    transition: top .5s;
    opacity: 1;

    &:after {
        display: block;
        content: '';
        width: 20px;
        position: relative;
        left: -11px;
        height: 100%;
        background: transparent;
        border-top: $caret-stroke-width solid $primary;
        border-bottom: $caret-stroke-width solid $primary;
    }
}

.link-name{
    transform: rotate(-90deg) translateX(-60px);
    text-align: center;
    width: 100px;
    transform-origin: left;
}
