@import "../../style/variables";

.navigation-link:hover {
  color: $primary
}

@keyframes hide-navbar-animation {
  from {top: 0;}
  to {top: -80px;}
}

.navbar--hidden {
  animation-name: hide-navbar-animation;
  animation-duration: 1s;
  top: -80px;
}

.navbar-container {
  display: flex;
  width: 100%;
  position: fixed;
  background: #2b2b2b;
  opacity: 0.8;
  justify-content: flex-end;
  padding: 20px 40px 20px 20px;
  font-size: 16pt;
  font-weight: bold;
  font-family: 'Roboto Slab', serif;
  color: $text;
  z-index: 20;

  @media (max-width: $media-medium) {
    justify-content: space-between;
    margin-left: 0;
    padding: 20px;
  }

  .active-link {
    color: $primary
  }

  @media (max-width: $media-small){
    font-size: 14pt;
    margin-left: 20px;
    right: 5px;
  }

  span {
    cursor: pointer;
    display: inline;
    margin-left: 90px;
    text-transform: uppercase;
    text-shadow: -1px -1px 1px #212121, 1px -1px 1px #212121, -1px 1px 1px #212121, 1px 1px 1px #212121;

    @media (max-width: $media-small){
      font-size: 14pt;
      margin-left: 20px;
    }

    a {
      cursor: pointer;
      color: $text;
      text-decoration: none;
    }
  }
}
