@import "../../style/variables";

.showcases {
  position: relative;
  display: flex;
  align-items: center;
  -webkit-overflow-scrolling: touch;
  z-index: 5;
  height: 100%;
  width: 100%;
  padding: 100px 20px 20px 20px;
  flex-direction: column;
  overflow: auto;
  overflow-x: hidden;

  .showcases-content {
    max-width: 1300px;
    display: flex;
    flex-direction: column;
  }

  h1 {
    font-size: 48pt;
    text-align: center;
    margin-bottom: 40px;
  }

  .print {
    position: fixed;
    bottom: 80px;
    right: 20px;
    background: rgba(255,255,255, 0.5);
    height: 56px;
    width: 56px;
    border-radius: 10px;
    padding: 10px;
    cursor: pointer;

    @media (max-width: $media-medium) {
      display: none;
    }

    img {
      height:100%;
    }
  }
}
