@import "../../style/variables";


.technologies-wrapper {
  width: 100%;
  margin-left: $md-margin;
  margin-right: $md-margin;
  @media (max-width: $media-medium) {
    background-color: $background;
  }

  padding: $lg-padding;

  .technologies-header {
    text-align: center;

    &:after {
      display: block;
      content: '';
      width: 100%;
      position: relative;
      height: 100%;
      background: transparent;
      border-bottom: 1px solid $primary;
    }
  }
}
