@import "../../../style/variables";

$media-medium: 816px;

.right-block {
  position: absolute;
  width: 400px;
  height: 80%;
  right: -200px;
  top: 100px;

  @media (max-width: $media-medium) {
    top: 40px;
    width: 200px;
    right: -160px;
  }

  .right-block-content{
    width: 100%;
    height: 100%;
    position: relative;

    @media (max-width: $media-small){
      display: none;
    }

    .image-addon {
      cursor: pointer;
      position: absolute;
      top: 10%;
      height: 80%;
      width: 300px;
      left: -240px;
      padding: 20px;

      @media (max-width: $media-medium){
        width: 200px;
        left: -160px;
      }

      @media (max-width: $media-small){
        padding: 10px;
        width: 60px;
        left: -20px;
      }

      .page-number {
        font-size: 72pt;
        color: black;
      }

      .aboutUs-link {
        color: black;
        font-size: 28pt;
        position: absolute;
        bottom: 20px;

        p {
          @media (max-width: $media-small){
            transform: rotate(-90deg);
            width: 300px;
            height: 300px;
          }
        }
      }

      img {
        height: 30px;
        margin-left: 20px;

        @media (max-width: $media-medium){
          display: block;
          margin-left: 0;
        }
      }
    }
  }
}

.title-container {
  position: relative;
  top: 20%;

  font-style: normal;
  font-weight: bold;
  font-size: 96px;
  line-height: normal;

  @media (max-width: $media-medium) {
    margin-left: 20px;
  }

  & p {
    margin: 0;
    line-height: 100px;
  }

  & #koi {
    top: 20px;
    left: 20px;
    color: $primary;
  }

  & #techs {
    top: 110px;
    left: 20px;
    color: white;
  }

  & #tagline {
    top: 230px;
    left: 20px;
    width: 90%;
    text-align: left;
    font-size: 32px;
    color: $text;
    line-height: 40px;
  }
}


