@import "../../style/variables";

$tool-img-size: 80px;
$tool-img-size-phone: 60px;

.slider {
  display: grid;
  grid-template-columns: repeat(auto-fill, 100px);

  @media (max-width: $media-medium) {
    grid-template-columns: repeat(auto-fill, 80px);
  }

  justify-content: center;

  width: 100%;
  padding-bottom: $sm-margin;

}

.tool-image-wrapper {
  width: $tool-img-size !important;
  height: $tool-img-size;

  margin: $md-padding;


  @media (max-width: $media-medium) {
    margin: $md-margin;
    width: $tool-img-size-phone !important;
    height: $tool-img-size-phone;
  }

  @media print {
    margin-bottom: 25px;
  }

  .tool-alt {
    font-size: 10pt;
    padding-top: 5px;
    text-align: center;
    width: 100%;
    color: $primary;
  }

  .tool-image {
    width: 100%;
    height: 100%;

    filter: grayscale(100%);

    @media (max-width: $media-medium) {
      filter: none;
    }

    &:hover {
      -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
      transform: scale(1.3);
      filter: none;
    }
  }
}
