@import "../../../style/variables";

.points-list {
  color: $text;
  font-size: 16pt;
  line-height: 36pt;

  ul {
    @media (max-width: $media-medium) {
      padding: 0;
    }
  }

  li {
    display: block;

    .digits {
      margin-right: 40px;
      color: $text-secondary
    }

    &.active {
      color: $primary
    }

    transition: color 0.5s;
  }
}

.capabilities {
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  align-items: center;

  .capability {
    flex: 50%;
    max-width: 300px;

    &:last-child {
      margin-top: -70px;
    }
  }
}
