@import "variables";

.fade-in {
  transform: translateX(-200px);
  transition: transform .5s .5s cubic-bezier(0.165, 0.84, 0.44, 1), opacity .5s .5s ;
  opacity: 0;

  &.fade-right {
    transform: translateX(200px);
  }

  @media (max-width: $media-medium) {
    opacity: 1;
    transform: translateX(0);
  }
}

.focused {
  .fade-in {
    opacity: 1;
    transform: translateX(0);
  }
}
