@import "../../style/variables";

$sqrt3: 1.7320508075688772;
$edgeSize: 160px;
$hexWidth: $edgeSize * 2;
$hexHeight: $sqrt3 * $edgeSize;
$borderHover: 16px;
$soft-white: $primary;
$ease-out-expo: cubic-bezier(0.19, 1, 0.22, 1);
$ease-out-quart: cubic-bezier(0.165, 0.84, 0.44, 1);

.hex-parent-container {
  display: flex;
  position: relative;
  width: 100%;
  height: 100%;

  max-height: 500px;
  max-width: 500px;

  &:hover{
    svg {
      transform: scale(1.05);
      transition: all .5s $ease-out-quart;
    }

    #scalable-stroke{
      stroke-width: 8px;
      transition: all .5s $ease-out-quart;
    }
  }

  svg {
    width: 100%;
    height: 100%;
  }

  .text-container {
    width: 70%;
    height: 50%;
    position: absolute;
    display: flex;
    left: 15%;
    top: 25%;

    align-items: center;
    justify-content: center;

    p {
      font-family: 'Roboto Slab', serif !important;
      margin: 0;
      text-align: center;
      display: inline-block;
    }
  }
}
