@import "../../style/variables";

.showcase {
  margin: 50px 0 50px 0;
  display: flex;
  padding: 40px;
  flex-direction: column;
  color: $text !important;
  height: 100%;

  .showcases-details {
    display: flex;
    margin: 16px $md-margin;

    @media (max-width: $media-medium) {
      flex-direction: column;
    }

    .project-details {
      width: 100%;
    }

    .case-image {
      width: calc(33% - 8px);
      height: 33%;
      margin-right: 16px;

      @media (max-width: $media-medium) {
        width: 100%;
        height: auto;
      }
    }

    .case-description {
      width: 100%;
      padding: $md-padding $lg-padding;
      margin-bottom: 16px;
      @media (max-width: $media-medium) {
        background: $background;
      }

      &::first-letter {
        color: $primary;
        font-size: 18px;
        font-weight: bolder;
      }
    }

    .carousel {
      flex-grow: 2;
    }
  }

  @media (max-width: $media-medium) {
    padding: 10px;
  }

  &.reverse {
    .showcase-content {
      flex-direction: row-reverse;
    }

    h2 {
      &:after {
        left: auto;
        right: 10%;
      }
    }
  }

  h2 {
    text-align: center;
    font-size: 36pt;
    margin-bottom: 20px;
    position: relative;

    @media (max-width: $media-medium) {
      font-size: 24pt;

      &:after {
        display: none !important;
      }
    }

    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      display: block;
      width: 50%;
      left: 10%;
      border-top: 1px solid $primary;
    }
  }

  .showcase-content {
    display: flex;
    flex-direction: row;

    .case-description {
      flex-grow: 2
    }

    @media (max-width: $media-medium) {
      flex-direction: column !important;
    }

    .preview {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    > div {
      flex: 1;
      display: flex;
      flex-direction: column;

      > div {
        padding: 20px;
      }

      .text {
        flex: 1;
        text-align: justify;
      }
    }

  }

  .technologies {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    //idk
    @media (max-width: $media-medium) {
      margin-right: 16px;
    }

    > section {
      margin-bottom: 16px;
    }

    @media (max-width: $media-medium) {
      flex-direction: column !important;
    }
  }
}
