@import "../../style/variables";
@import "../../style/fadeIn";

.application-container {
  position: fixed;
  font-family: 'Roboto Slab', serif;
  top: 0;
  left: 120px;
  width: calc(100% - 120px);
  height: 100%;
  overflow: hidden;

  &.smooth {
    -webkit-overflow-scrolling: touch;
  }

  &.rough {
    -webkit-overflow-scrolling: auto;
  }

  @media (max-width: $media-medium){
    left: 0;
    width: 100%;
    height: 100%;
    overflow-y: visible;
  }
}

.hide-medium {
  @media (max-width: $media-medium) {
    display: none !important;
  }
}

.large-page-number {
  font-size: 256pt;
  font-weight: bold;
  position: absolute;
  color: #212121;

  @media (max-width: $media-small) {
    right: -40px !important;
    color: #333;
  }

  &.one {
    left: 100px;
    top: -20px;
  }

  &.two {
    bottom: 20px;
    right: 100px;
  }

  &.three {
    left: 60px;
    top: 200px;
  }

  &.four {
    right: 60px;
    top: 0;
  }
}

.main-block {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  @media (max-width: $media-medium) {
    position: relative;
    flex-direction: column-reverse;
  }
}

.block {
  flex: 1;
  justify-content: center;
  display: inline-block;
  position: relative;
  padding: 20px;
  box-sizing: border-box;

  &.flexible {
    display: flex;
  }

  &.attaches-right{
    &:after,
    &:before {
      text-align: right;
    }
  }

  &.attaches-center{
    &:after,
    &:before {
      text-align: center;
    }
  }

  &:after,
  &:before {
    color: $text-secondary;
    padding: 20px;
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    text-align: left;
    left: 0;
  }

  &:after {
    content: attr(data-after);
    bottom: -100px;

    @media (max-width: $media-medium) {
      bottom: -30px;
    }
  }

  &:before {
    content: attr(data-before);
    top: -100px;

    @media (max-width: $media-medium) {
      top: -30px;
    }
  }

  &.flex {
    display: flex;
  }

  &.center {
    align-items: center;
    justify-content: center;
  }

  @media (max-width: $media-medium) {
    top: 30px;
    width: 100%;
    padding: 20px;
  }

  h2 {
    margin: 0;
    width: 100%;
    text-align: center;

    @media (max-width: $media-medium) {
      font-size: 64px;
      margin-bottom: 60px;
    }
  }

  p {
    text-align: justify;
    color: $text;
    font-size: 14pt;
    line-height: 30px;
  }
}

div.primary {
  background: $primary;
}

.image-overlay {
  position: relative;
  height: 100%;

  img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0.4;
  }
}

h1, h2, h3, h4 {
  color: $primary;
}

h2 {
  font-size: 96px;
}
