@import "../../style/variables";

.section {
    background: transparent;
    width: 100%;
    height: 100%;
    color: white;
    box-sizing: content-box;

    @media (max-width: $media-medium) {
        margin-top: 60px;
        height: auto;
        min-height: 100%;
    }

    &:last-of-type {
        margin-bottom: 100px;
    }
}
